<template>
    <div>
      <van-nav-bar
        title="商品列表"
        left-arrow
        @click-left="$router.go(-1)"
      />
      <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch"
      show-action
      @click="$router.push('/search')">
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <div class="sort-btns">
      <div class="sort-item" @click="toggle('all')">综合</div>
      <div class="sort-item" @click="toggle('sales' )">销量</div>
      <div class="sort-item" @click="toggle('price')">价格</div>
    </div>

    <goodsItem v-for="item in productItem" :key="item.goods_id" :item="item"></goodsItem>
    </div>

</template>

<script>
import { getProduct } from '@/api/product'
import goodsItem from '@/components/goodsItem.vue'
export default {
  name: 'searchlistIndex',
  data () {
    return {
      obj: {
        sortType: '',
        sortPrice: '',
        categoryId: '',
        goodsName: '',
        page: 1
      },
      num: '0',
      productItem: []
    }
  },
  components: {
    goodsItem
  },
  methods: {
    async toggle (result, num) {
      this.obj.sortType = result
      if (this.obj.sortType === 'price') {
        this.obj.sortPrice = this.num
        if (this.num === '1') {
          this.num = '0'
        } else {
          this.num = '1'
        }
      }
      this.getProduct()
    },
    async getProduct () {
      const res = await getProduct(
        {
          sortType: this.obj.sortType,
          sortPrice: this.obj.sortPrice,
          categoryId: this.$route.query.categoryId,
          goodsName: this.querySearch,
          page: this.obj.page
        }
      )
      this.productItem = res.data.list.data
    }
  },
  async created () {
    this.getProduct()
  },
  computed: {
    querySearch () {
      return this.$route.query.search
    }
  }

}
</script>

<style scoped>
  .sort-btns{
    display: flex;
    line-height: 36px;
    height: 36px;
  }
  .sort-item{
    text-align: center;
    font-size: 16px;
    flex: 1;
  }
  .tool{
    font-size: 24px;
    line-height: 40px;
    height: 40px;
    color: red;
    font-weight:900;
  }
</style>
